import { Editor, Element, Range, Text, Transforms } from "slate"

// block就是paragraph, codeBlock这种
const EditorBasics = {
  // 概念：Secto就是所选的区域 Yots就是头和尾巴 yot就是头或者尾
  // block是path的第一个数字的东西，包括paragraph和codeBlock
  // block级别的type包括textBlock, codeBlock
  // block里面的东西各自有各自的命运。。。不管了

  // callback就是(node, path) => {... return boolean}
  // at不填就默认匹配selection所选的区域的东西所涉及到的所有nodes
  iterate: (editor, match, at) => {
    const [result] = Editor.nodes(editor, {
      match,
      at,
    })
    return result
  },
  isSectoEmpty: (editor) => {
    const { anchor, focus } = editor.selection
    if (anchor.offset !== focus.offset) return false
    if (anchor.path.length !== focus.path.length) return false
    for (let i = anchor.path.length - 1; i >= 0; i--) {
      if (anchor.path[i] !== focus.path[i]) {
        return false
      }
    }
    return true
  },
  // 获取start和end两个的path
  getSectoYotsPathStartAndEnd: (editor) => {
    const [, maxPathLength] =
      EditorBasics.getSectoYotsPathMinAndMaxLength(editor)
    const { anchor, focus } = editor.selection
    let path1 = anchor.path
    let path2 = focus.path
    if (path1.length < maxPathLength) {
      const arr = new Array(maxPathLength).fill(0)
      path1.forEach((value, index) => {
        arr[index] = value
      })
      path1 = arr
    }
    if (path2.length < maxPathLength) {
      const arr = new Array(maxPathLength).fill(0)
      path2.forEach((value, index) => {
        arr[index] = value
      })
      path2 = arr
    }
    let isPath1BiggerThanPath2 = false
    for (let i = 0; i < maxPathLength; i++) {
      if (path1[i] > path2[i]) {
        isPath1BiggerThanPath2 = true
        break
      }
    }
    if (isPath1BiggerThanPath2) {
      return [focus.path, anchor.path]
    } else {
      return [anchor.path, focus.path]
    }
  },
  getSectoYotsPathMinAndMaxLength: (editor) => {
    const { anchor, focus } = editor.selection
    const len1 = anchor.path.length
    const len2 = focus.path.length
    if (len1 <= len2) {
      return [len1, len2]
    } else {
      return [len2, len1]
    }
  },
  // Nth换算成index是n-1
  // 这个NthNumAll比如[2,3,8]到[2,5,7]的2nd就返回[3,5]
  getSectoYotsPathNthNumSpan: (editor, n) => {
    const { anchor, focus } = editor.selection
    const num1 = anchor.path[n - 1]
    const num2 = focus.path[n - 1]
    if (num1 <= num2) {
      return [num1, num2]
    } else {
      return [num2, num1]
    }
  },
  // 比如说自己是[3,2,5]，想知道第一个数字最大能是多少 path就是自己当前的path
  getPathNthNumMax: (editor, path, n) => {
    // slice(0,3) 获得[x,x,x]
    return Editor.end(editor, path.slice(0, n - 1)).path[n - 1]
  },
  isSectoYotsPathNthNumSame: (editor, n) => {
    const { anchor, focus } = editor.selection
    return anchor.path[n - 1] === focus.path[n - 1]
  },
  // 就是前n个数字是不是一样的(之前必须确保有这么多位)
  isSectoYotsPathInitialNumsSame: (editor, n) => {
    for (let i = n - 1; i >= 0; i--) {
      if (!EditorBasics.isSectoYotsPathNthNumSame(editor, n)) return false
    }
    return true
  },
  // 返回一个[]比如["paragraph", "codeBlock"]
  // Secto为空也会返回所在的block的type
  getSectoBlockTypes: (editor) => {
    const types = []
    EditorBasics.iterate(editor, (node, path) => {
      if (path.length === 1) {
        const type = node.type
        if (!types.includes(type)) {
          types.push(type)
        }
      }
    })
    return types
  },
  // 给定一个{text:"..."}这种node的path，然后将它的文本设置为text
  setTextAt: (editor, text, at) => {
    Transforms.insertText(editor, text, {
      at,
    })
  },
  // 给定当前textBlock的index，判断当前textblock是否有replaced并替换为replacedBy
  // 目前用来匹配某个textBlock中所有一般的文本
  // codeSpan禁止输入\t这种东西！
  replaceTextInTextBlock: (editor, index, replaced, replacedBy) => {
    EditorBasics.iterate(
      editor,
      (node, path) => {
        if (path[0] === index && path.length === 2) {
          if (node?.type === undefined) {
            EditorBasics.setTextAt(
              editor,
              node.text.replaceAll(replaced, replacedBy),
              path
            )
            // 这里匹配到了
          }
        }
      },
      [index]
    )
  },
  // place填"start" 就是在CodeLine最开始加text
  addTextInCodeLine: (editor, path, text, place) => {
    if (place === "start") {
      path.push(0)
      Transforms.insertText(editor, text, {
        at: {
          path,
          offset: 0,
        },
      })
    }
  },
  removeTextInCodeLine: (editor, path, text, place) => {
    if (place === "start") {
      path.push(0)
      const node = Editor.node(editor, path)
      const previousText = node[0].text
      const regex = new RegExp("^" + text)
      if (regex.test(previousText)) {
        Transforms.delete(editor, {
          at: {
            path,
            offset: 0,
          },
          distance: text.length,
          unit: "character",
        })
      }
    }
  },
  isMultipleBlocksSelected: (editor) => {
    // 不能是[x,..]和[y,...]这种位置
    const { selection } = editor
    return selection.anchor.path[0] !== selection.focus.path[0]
  },
}
export default EditorBasics
