import React, { useContext } from "react"
import s from "./Seg.module.scss"
import IntertextContext from "./IntertextContext"

function Seg(props) {
  const { segData } = props
  const typeNum = segData[1]
  const text = segData[2]

  const intertextContext = useContext(IntertextContext)
  const { operatedData, setOperatedData, setRightClickPanelData } =
    intertextContext

  const classNames = [
    s.chatSeg + " " + s.leftSeg,
    s.chatSeg + " " + s.rightSeg,
    s.centerSeg,
  ]

  let isFocused = false
  if (operatedData.type === "seg" || operatedData.type === "segs") {
    isFocused = operatedData.content.includes(segData)
  }

  const onRightClick = (ev) => {
    ev.preventDefault()
    setOperatedData({
      type: "seg",
      content: [segData],
    })
    setRightClickPanelData({
      isShow: true,
      xY: [ev.clientX, ev.clientY],
      choices: [
        // ["edit", "Edit", "editSegText"],
        ["delete", "Delete", "deleteOneSeg"],
      ],
    })
  }

  return (
    <div
      className={classNames[typeNum] + " " + (isFocused ? s.focused : "")}
      onContextMenu={onRightClick}
    >
      {text}
    </div>
  )
}
export default Seg
