import s from "./ActionPanel.module.scss"
import { useContext, useState } from "react"
import ActionPanelContext from "./ActionPanelContext"
import { ReactEditor } from "slate-react"
import EditorUtils from "./EditorUtils"
import { getBasicStamp } from "../../utils/timeStamp"

// 注意事项 不要在这里用useState！！！

function ActionPanel(props) {
  const {
    isShowActionPanel,
    setIsShowActionPanel,
    actionPanelData,
    setActionPanelData,
  } = useContext(ActionPanelContext)

  const panelNodes = () => {
    if (!isShowActionPanel) {
      return <></>
    } else {
      if (
        actionPanelData.type === "insertLink" ||
        actionPanelData.type === "editLink"
      ) {
        const { text, textMust, url, urlMust, editor } = actionPanelData
        const setText = (ev) => {
          setActionPanelData({ ...actionPanelData, text: ev.target.value })
        }
        const setURL = (ev) => {
          setActionPanelData({ ...actionPanelData, url: ev.target.value })
        }
        const goToLink = () => {
          if (url === "") {
            setActionPanelData({
              ...actionPanelData,
              urlMust: url === "" ? "Can't be empty" : null,
            })
          } else {
            window.open(url, "_blank")
          }
        }
        const cancel = () => {
          ReactEditor.focus(editor)
          setIsShowActionPanel(false)
        }
        const ok = () => {
          setActionPanelData({
            ...actionPanelData,
            textMust: text === "" ? "Can't be empty" : null,
            urlMust: url === "" ? "Can't be empty" : null,
          })
          if (url !== "" && text !== "") {
            ReactEditor.focus(editor)
            if (actionPanelData.type === "insertLink") {
              EditorUtils.insertLink(editor, text, url)
            } else if (actionPanelData.type === "editLink") {
              EditorUtils.editLink(editor, text, url)
            }
            setIsShowActionPanel(false)
          }
        }
        return (
          <>
            <div className={s.panelFrame}>
              <div className={s.panel + " " + s.linkPanel}>
                <div className={s.panelTitle}>
                  {actionPanelData.type === "insertLink"
                    ? "Insert link"
                    : "Edit link"}
                </div>
                <div className={s.text}>
                  <div className={s.label}>Text</div>
                  <input
                    className={s.input}
                    type="text"
                    spellCheck={false}
                    value={text}
                    onChange={setText}
                    placeholder={textMust}
                    autoFocus
                  />
                </div>
                <div className={s.url}>
                  <div className={s.label}>URL</div>
                  <input
                    className={s.input}
                    type="text"
                    spellCheck={false}
                    value={url}
                    onChange={setURL}
                    placeholder={urlMust}
                  />
                </div>
                <div className={s.urlPrompt}>
                  <div className={s.goToURL} onMouseDown={goToLink}>
                    Go to this URL
                  </div>
                  <div className={s.rememberToWriteHead}>
                    (To go to another website successfully, you should include
                    "https" or "http" in URL)
                  </div>
                </div>

                <div className={s.buttons}>
                  <div className={s.cancel} onClick={cancel}>
                    Cancel
                  </div>
                  <div className={s.ok} onClick={ok}>
                    OK
                  </div>
                </div>
              </div>
            </div>
          </>
        )
      } else if (actionPanelData.type === "setMetaData") {
        const {
          title,
          isTitleFocused,
          toBottom,
          id,
          baseURL,
          now,
          editor,
          onOK,
        } = actionPanelData
        const setTitle = (title) =>
          setActionPanelData({ ...actionPanelData, title })
        const setIsTitleFocused = (isTitleFocused) => {
          setActionPanelData({ ...actionPanelData, isTitleFocused })
        }
        const toggleToBottom = () =>
          setActionPanelData({ ...actionPanelData, toBottom: !toBottom })
        const setBaseURL = (baseURL) =>
          setActionPanelData({ ...actionPanelData, baseURL })
        const processedTitle = title.replace(/[\\/:*?"<>|]/g, "#")
        const previewedTitle =
          processedTitle.length > 40
            ? processedTitle.substring(0, 32) +
              "..." +
              processedTitle.substring(processedTitle.length - 8)
            : processedTitle
        const basicStamp = getBasicStamp(now)
        const cancel = () => {
          ReactEditor.focus(editor)
          setIsShowActionPanel(false)
        }
        const ok = () => {
          ReactEditor.focus(editor)
          onOK(actionPanelData)

          setIsShowActionPanel(false)
        }
        return (
          <>
            <div className={s.panelFrame}>
              <div className={s.panel + " " + s.metaDataPanel}>
                <div className={s.panelTitle}>Set properties</div>

                <div className={s.title}>
                  <div className={s.label}>Title</div>
                  <div
                    className={s.titleStuff}
                    onFocus={() => setIsTitleFocused(true)}
                    onBlur={() => setIsTitleFocused(false)}
                  >
                    <input
                      className={s.input}
                      type="text"
                      spellCheck={false}
                      autoFocus
                      value={title}
                      onChange={(ev) => setTitle(ev.target.value)}
                    />
                    <div className={s.exportFileName}>
                      <div>When exported, file name will be like: </div>
                      <div className={isTitleFocused ? s.highlight : ""}>
                        {basicStamp +
                          (previewedTitle.length !== 0
                            ? " " + previewedTitle
                            : "") +
                          ".json"}
                      </div>
                    </div>
                  </div>
                </div>
                {id !== "" && (
                  <div className={s.id}>
                    <div className={s.label}>ID</div>
                    <div className={s.idString}>{id}</div>
                  </div>
                )}

                <div className={s.toBottom}>
                  <span>When opened, go to</span>
                  <span className={s.toggle} onClick={toggleToBottom}>
                    {toBottom ? "Bottom" : "Top"}
                  </span>
                </div>
                <div className={s.baseURL}>
                  <div className={s.label}>Base URL</div>
                  <input
                    className={s.input}
                    type="text"
                    spellCheck={false}
                    value={baseURL}
                    onChange={(ev) => setBaseURL(ev.target.value)}
                  />
                </div>
                <div className={s.buttons}>
                  <div className={s.cancel} onClick={cancel}>
                    Cancel
                  </div>
                  <div className={s.ok} onClick={ok}>
                    OK
                  </div>
                </div>
              </div>
            </div>
          </>
        )
      } else if (actionPanelData.type === "chooseFile") {
        const { filesData, onOK, editor, chosenFileId } = actionPanelData
        const setChosenFileId = (id) => {
          setActionPanelData({ ...actionPanelData, chosenFileId: id })
        }
        const cancel = () => {
          ReactEditor.focus(editor)
          setIsShowActionPanel(false)
        }
        const ok = () => {
          ReactEditor.focus(editor)
          onOK(chosenFileId)
        }
        const fileBars = () =>
          filesData.map((fileData) => (
            <div
              key={fileData[0]}
              className={
                s.fileBar + " " + (fileData[0] === chosenFileId ? s.chosen : "")
              }
              onClick={() => setChosenFileId(fileData[0])}
            >
              {fileData[1] === "" ? "[No title]" : fileData[1]}
            </div>
          ))
        return (
          <div className={s.panelFrame}>
            <div className={s.panel + " " + s.chooseFilePanel}>
              <div className={s.panelTitle}>Choose and open</div>
              <div className={s.files}>{fileBars()}</div>
              <div className={s.buttons}>
                <div className={s.cancel} onClick={cancel}>
                  Cancel
                </div>
                <div className={s.ok} onClick={ok}>
                  OK
                </div>
              </div>
            </div>
          </div>
        )
      } else {
        setIsShowActionPanel(false)
        return <div />
      }
    }
  }

  return (
    <>
      <div className={isShowActionPanel ? s.blur : ""}>{props.children}</div>
      {isShowActionPanel && <div className={s.shadow} />}
      {isShowActionPanel && panelNodes()}
    </>
  )
}
export default ActionPanel
