// 光是Date.now()得到的epochtime是没法.getFullYear()这些的 必须new Date(now)

// zonedStamp 20000404-200420-E9
export const getZonedStamp = (now) => {
  if (typeof now === "number") {
    now = new Date(now)
  }
  const year = now.getFullYear().toString().padStart(4, "0")
  const month = (now.getMonth() + 1).toString().padStart(2, "0")
  const day = now.getDate().toString().padStart(2, "0")
  const hours = now.getHours().toString().padStart(2, "0")
  const minutes = now.getMinutes().toString().padStart(2, "0")
  const seconds = now.getSeconds().toString().padStart(2, "0")
  const timezoneOffset = now.getTimezoneOffset()
  let prefix
  if (timezoneOffset > 0) prefix = "W"
  if (timezoneOffset < 0 && timezoneOffset !== -720) prefix = "E"
  const suffix = timezoneOffset % 60 !== 0 ? "X" : ""
  const timezone = prefix + Math.abs(timezoneOffset / 60).toString() + suffix
  return year + month + day + "-" + hours + minutes + seconds + "-" + timezone
}

// basicStamp 20000404-200420
export const getBasicStamp = (now) => {
  if (typeof now === "number") {
    now = new Date(now)
  }
  const year = now.getFullYear().toString().padStart(4, "0")
  const month = (now.getMonth() + 1).toString().padStart(2, "0")
  const day = now.getDate().toString().padStart(2, "0")
  const hours = now.getHours().toString().padStart(2, "0")
  const minutes = now.getMinutes().toString().padStart(2, "0")
  const seconds = now.getSeconds().toString().padStart(2, "0")
  return year + month + day + "-" + hours + minutes + seconds
}

// epochStamp 0001670404040404 16位
export const getEpochStamp = (now) => {
  if (typeof now === "number") {
    now = new Date(now)
  }
  return now.toString().padStart(16, "0")
}

// realStamp 2020/04/04 20:04
export const getRealStamp = (now) => {
  if (typeof now === "number") {
    now = new Date(now)
  }
  const year = now.getFullYear()
  const month = now.getMonth() + 1 // Note: January is month 0
  const day = now.getDate()
  const hour = String(now.getHours()).padStart(2, "0")
  const minute = String(now.getMinutes()).padStart(2, "0")

  return `${year}/${month}/${day} ${hour}:${minute}`
}

