import React, { useEffect, useState } from "react"
import s from "./WikipediaSearch.module.scss"
import "./Wikipedia.content.scss"
import "@fortawesome/fontawesome-free/js/brands"
import "@fortawesome/fontawesome-free/js/solid"
import "@fortawesome/fontawesome-free/js/fontawesome"
import { useNavigate } from "react-router-dom"

function WikipediaSearch() {
  useEffect(() => {
    document.title = "wikipedia tool"
  }, [])
  const [inputText, setInputText] = useState("")
  const [prompts, setPrompts] = useState([])
  const [isSearching, setIsSearching] = useState(false)
  const promptAmount = 5
  const navigate = useNavigate()

  // useEffect(() => {
  //   alert("Sorry I know here are still many bugs yet to be fixed @_@")
  // }, [])

  useEffect(() => {
    const timeout = setTimeout(() => {
      if (inputText.trim()) {
        getPrompts(inputText.trim())
      } else {
        setPrompts([])
      }
    }, 200)
    return () => {
      clearTimeout(timeout)
    }
  }, [inputText.trim()])

  useEffect(() => {
    document.addEventListener("keydown", onKeyDown, false)
    return () => {
      document.removeEventListener("keydown", onKeyDown, false)
    }
  })
  const onKeyDown = (ev) => {
    if (inputText !== "" && ev.key === "Enter") {
      search()
    }
  }
  const search = () => {
    const url =
      "https://en.wikipedia.org/w/api.php?" +
      new URLSearchParams({
        origin: "*",
        action: "opensearch",
        search: inputText,
        limit: promptAmount,
      })
    fetch(url)
      .then((res) => res.json())
      .then((json) => {
        if (json[1].length === 0) {
          alert("No relevant result.")
          setIsSearching(false)
        } else {
          navigate(
            json[3][0].replace("https://en.wikipedia.org/wiki", "/wikipedia")
          )
        }
      })
  }

  const getPrompts = (searchText) => {
    const url =
      "https://en.wikipedia.org/w/api.php?" +
      new URLSearchParams({
        origin: "*",
        action: "opensearch",
        search: searchText,
        limit: promptAmount,
      })
    fetch(url)
      .then((res) => res.json())
      .then((json) => {
        const newPrompts = []
        for (let i = 0; i < promptAmount; i++) {
          let url
          if (json[3][i] === undefined) {
            url = ""
          } else {
            url = json[3][i].replace(
              "https://en.wikipedia.org/wiki",
              "/wikipedia"
            )
          }
          newPrompts.push({
            title: json[1][i],
            url,
          })
        }
        setPrompts(newPrompts)
      })
  }

  return (
    <>
      <div className={s.title}>WIKIPEDIA TOOL</div>
      <div className={s.searchFrame}>
        <div className={s.searchBar}>
          <input
            onChange={(ev) => {
              if (!isSearching) {
                setInputText(ev.target.value)
              }
            }}
            value={inputText}
            className={s.input}
            placeholder={"Search in Wikipedia (English)"}
            spellCheck={false}
          />
          <div onClick={() => search()}>
            <i className={"fa-solid fa-magnifying-glass " + s.searchIcon} />
          </div>
        </div>
        {prompts && prompts[0] && prompts[0].title !== undefined && (
          <div className={s.promptFrame}>
            {prompts.map((prompt) => (
              <div
                className={s.prompt}
                key={Math.random()}
                onClick={() => navigate(prompt.url)}
              >
                {prompt.title}
              </div>
            ))}
          </div>
        )}
      </div>
    </>
  )
}

export default WikipediaSearch
