import HomeDesktop from "./pages/Home/desktop/Home.js"
import HomeMobile from "./pages/Home/mobile/Home.js"
import { Navigate, Route, Routes } from "react-router-dom"
import Symbols from "./pages/Clipboard/Symbols"
import Test from "./pages/Test/Test"
import WikipediaSearch from "./pages/Wikipedia/WikipediaSearch"
import Wikipedia from "./pages/Wikipedia/Wikipedia"
import IntertextDesktop from "./pages/Intertext/desktop/Intertext"
import IntertextMobile from "./pages/Intertext/mobile/Intertext"
import Introduction from "./pages/Introduction/Introduction"
import Sculpt from "./pages/Sculpt/Sculpt"
import SculptStart from "./pages/Sculpt/SculptStart"
import { useMemo } from "react"
function App() {
  return (
    <>
      <Routes>
        <Route
          path={"/"}
          element={window.isMobile ? <HomeMobile /> : <HomeDesktop />}
        />
        {/* <Route path={"/introduction"} element={<Introduction />} /> */}
        <Route
          path={"/intertext"}
          element={window.isMobile ? <IntertextMobile /> : <IntertextDesktop />}
        />
        <Route path={"/sculpt"} element={<SculptStart />} />
        <Route path={"/clipboard/symbols"} element={<Symbols />} />
        <Route path={"/wikipedia"} element={<WikipediaSearch />} />
        <Route path={"/wikipedia/:title"} element={<Wikipedia />} />
        <Route path={"/test"} element={<Test />} />
        <Route path={"*"} element={<Navigate to={"/"} />} />
      </Routes>
    </>
  )
}

export default App
