import s from "./Intertext.module.scss"
import React, { useEffect, useRef, useState } from "react"
import "@fortawesome/fontawesome-free/js/brands"
import "@fortawesome/fontawesome-free/js/solid"
import "@fortawesome/fontawesome-free/js/fontawesome"
import Prompt from "../../../components/Prompt/desktop/Prompt"
import PromptContext from "../../../components/Prompt/desktop/PromptContext"
import RightClickPanel from "./RightClickPanel"
import Seg from "./Seg"
import IntertextContext from "./IntertextContext"

function Intertext() {
  useEffect(() => {
    document.title = "intertext"
  }, [])

  // How to use | demo
  // 原则：没有那么多精力，那就做最最关键的功能
  // Edit!!!

  const [segs, setSegs] = useState([])

  const [operatedData, setOperatedData] = useState({
    type: "none", // seg, segs, selectedText
    content: "", // seg或者segs这里直接应该是[[segKey, "0", "内容"],[...]]这种, selectedText就是"..."
  })
  const operations = {
    deleteSeg: () => {
      const newSegs = [
        ...segs.filter((seg) => !operatedData.content.includes(seg)),
      ]
      setSegs(newSegs)
    },
  }
  const [rightClickPanelData, setRightClickPanelData] = useState({
    isShow: false,
    xY: [0, 0],
    choices: [], // [["delete", "Delete", "deleteOneSeg"]] 第一个是图标 第二个是text 第三个是提醒做什么的
  })

  // 强制回收站 你删除的东西都会记录起来保存在localstorage里

  // 用index和时间戳标记key
  // ctrl+z 还早呢
  // 格式化复制 左:xxx [2023/3/6] 右:xxx
  // 导出图片 / 便于pdf打印

  // invent sth to take notes of coding (main text / comments in two different colors)

  const [whichSegToSend, setWhichSegToSend] = useState("L")
  const [textareaText, setTextareaText] = useState("")
  const [fileName, setFileName] = useState("Choose a file to continue")
  const fileInputRef = useRef()

  const [isShowTopShade, setIsShowTopShade] = useState(false)
  const [isShowBottomShade, setIsShowBottomShade] = useState(false)

  const [isShowPrompt, setIsShowPrompt] = useState(false)

  const [promptData, setPromptData] = useState({})

  useEffect(() => {
    setPromptData({
      title: "Notice",
      text:
        "The PC version of Intertext hasn't been developed for a while " +
        "(and its file data structure is now outdated compared to the mobile one!). " +
        "If you want to try the latest Intertext, " +
        "please use the mobile version (open this webpage on your phone, " +
        "or just right-click now and click [inspect] on this PC browser to simulate the mobile version)",
      buttons: [
        [
          "OK",
          () => {
            setIsShowPrompt(false)
          },
        ],
      ],
    })
    setIsShowPrompt(true)
  }, [])

  const clickFileInput = () => {
    fileInputRef.current.click() // 虽然没有提示但是是可以用的
  }
  const fileInputOnChange = () => {
    if (fileInputRef.current.value !== "") {
      const filePath = fileInputRef.current.value
      const fileName = filePath.split(/[/\\]/).pop()
      setFileName(fileName)
      fetch(URL.createObjectURL(fileInputRef.current.files[0]))
        .then((res) => res.json())
        .then((json) => {
          const tempSegs = []
          json.map((seg, index) => {
            tempSegs.push([index, ...seg])
          })
          setSegs(tempSegs)

          setTimeout(() => {
            goToBottom(true)
          }, 100)
        })
    }
  }
  const onClickSend = () => {
    if (textareaText.trim()) {
      const newSegs = [...segs]
      const sideNum = {
        L: 0,
        R: 1,
        C: 2,
      }
      newSegs.push([Date.now(), sideNum[whichSegToSend], textareaText.trim()])
      setTextareaText("")
      setSegs(newSegs)
      goToBottom(false)
    }
  }

  const onKeyDown = (ev) => {
    if (ev.shiftKey && ev.key === "ArrowLeft") {
      setWhichSegToSend("L")
      ev.preventDefault()
    }
    if (ev.shiftKey && ev.key === "ArrowRight") {
      setWhichSegToSend("R")
      ev.preventDefault()
    }
    if (ev.shiftKey && ev.key === "ArrowUp") {
      setWhichSegToSend("C")
      ev.preventDefault()
    }
    if (ev.key === "Enter") {
      onClickSend()
      ev.preventDefault()
    }
  }
  useEffect(() => {
    document.addEventListener("keydown", onKeyDown, false)
    return () => {
      document.removeEventListener("keydown", onKeyDown, false)
    }
  })
  ;["scroll", "resize"].map((eventType) => {
    window.addEventListener(eventType, () => {
      if (rightClickPanelData.isShow) {
        setRightClickPanelData({ isShow: false, xY: [], choices: [] })
        // useState会检查匹配与否！！！
      }
      if (operatedData.type === "seg") {
        setOperatedData({ type: "none", content: "" })
      }
    })
  })

  const content = segs.map((seg) => {
    return <Seg key={seg[0]} segData={seg} />
  })

  // 随时保存segs内容到localStorage
  // 可能要提醒一下目前localStorage这个存了多大了 1MB=1048576字节
  // useEffect(() => {
  //   if (segs.length !== 0) {
  //     localStorage.setItem("intertext-unsaved-text", JSON.stringify(segs))
  //   }
  //   return () => {
  //     console.log(localStorage.getItem("intertext-unsaved-text"))
  //   }
  // }, [segs])

  // Powered by React
  // How to use?

  const saveAndDownload = () => {
    const now = new Date()
    const year = now.getFullYear().toString().padStart(4, "0")
    const month = (now.getMonth() + 1).toString().padStart(2, "0")
    const day = now.getDate().toString().padStart(2, "0")
    const hours = now.getHours().toString().padStart(2, "0")
    const minutes = now.getMinutes().toString().padStart(2, "0")
    const seconds = now.getSeconds().toString().padStart(2, "0")
    const timeStamp = year + month + day + "-" + hours + minutes + seconds

    const textFile = new Blob(
      [JSON.stringify(segs.map((seg) => [seg[1], seg[2]]))],
      { type: "text/plain" }
    )
    const url = URL.createObjectURL(textFile)
    const link = document.createElement("a")
    link.href = url
    link.download = timeStamp + " test.json"
    link.click()
    window.URL.revokeObjectURL(url)
  }

  const changeWhichSegToSend = (which) => {
    if (which !== whichSegToSend) {
      setWhichSegToSend(which)
    }
  }

  const whichSegToSendButtons = ["L", "C", "R"].map((which) => {
    const className =
      s.whichSegToSendButton +
      " " +
      (which === whichSegToSend ? s.selected : "")
    return (
      <div
        className={className}
        key={which}
        onClick={() => changeWhichSegToSend(which)}
      >
        {which}
      </div>
    )
  })

  const goToTop = (hasEffect) => {
    window.scrollTo({
      top: 0,
    })
    if (hasEffect) {
      setIsShowTopShade(false)
      setTimeout(() => {
        setIsShowTopShade(true)
      }, 10)
    }
  }
  const goToBottom = (hasEffect) => {
    window.scrollTo({
      top: 1000000,
    })
    if (hasEffect) {
      setIsShowBottomShade(false)
      setTimeout(() => {
        setIsShowBottomShade(true)
      }, 10)
    }
  }

  // context记笔记！

  return (
    <IntertextContext.Provider
      value={{
        operatedData,
        setOperatedData,
        operations,
        rightClickPanelData,
        setRightClickPanelData,
      }}
    >
      <div className={s.root}>
        <PromptContext.Provider
          value={{ isShowPrompt, setIsShowPrompt, promptData, setPromptData }}
        >
          <Prompt>
            <div className={s.segsFrame}>
              <div className={s.segsContent}>
                {content}
                {segs.length === 0 && <div className={s.noText}>NO TEXT</div>}
                <div className={s.emptySegsEnd} />
              </div>
              {isShowTopShade && <div className={s.topShade} />}
              {isShowBottomShade && <div className={s.bottomShade} />}
            </div>

            <div className={s.panels}>
              <div className={s.filePanel}>
                <div className={s.fileName} onClick={clickFileInput}>
                  {fileName}
                  <input
                    type="file"
                    className={s.fileInput}
                    ref={fileInputRef}
                    onChange={fileInputOnChange}
                  />
                </div>
                <div className={s.saveButton} onClick={saveAndDownload}>
                  Save
                </div>
              </div>
              <div className={s.segTextInputPanel}>
                <textarea
                  className={s.segTextInput}
                  spellCheck={false}
                  value={textareaText}
                  onChange={(ev) => setTextareaText(ev.target.value)}
                />
                <div className={s.buttons}>
                  <div className={s.whichSegToSendButtons}>
                    {whichSegToSendButtons}
                  </div>
                  <div className={s.sendButton} onClick={onClickSend}>
                    Send
                  </div>
                </div>
              </div>
              <div className={s.toTopOrBottom}>
                <div
                  className={s.toTopOrBottomIcon}
                  onClick={() => goToTop(true)}
                >
                  <i className="fa-solid fa-angle-up" />
                </div>
                <div
                  className={s.toTopOrBottomIcon}
                  onClick={() => goToBottom(true)}
                >
                  <i className="fa-solid fa-angle-down" />
                </div>
              </div>
            </div>
            {rightClickPanelData.isShow && <RightClickPanel />}
          </Prompt>
        </PromptContext.Provider>
      </div>
    </IntertextContext.Provider>
  )
}
export default Intertext
