import s from "./RightClickPanel.module.scss"
import React, { useContext } from "react"
import PromptContext from "../../../components/Prompt/desktop/PromptContext"
import IntertextContext from "./IntertextContext"

// (选中文本)复制，inspect，以及其他功能
// delete

function RightClickPanel() {
  const intertextContext = useContext(IntertextContext)
  const promptContext = useContext(PromptContext)

  const {
    operatedData,
    setOperatedData,
    operations,
    rightClickPanelData,
    setRightClickPanelData,
  } = intertextContext
  const { setIsShowPrompt, setPromptData } = promptContext

  const { isShow, xY, choices } = rightClickPanelData

  const iconClassNames = {
    edit: "fa-solid fa-pen",
    delete: "fa-solid fa-xmark",
  }

  const handlers = {
    deleteOneSeg: () => {
      setPromptData({
        title: "Delete this text?",
        content: operatedData.content[0][2],
        buttons: [
          [
            "Yep",
            () => {
              operations.deleteSeg()
              setRightClickPanelData({ isShow: false })
              setOperatedData({ type: "none" })
              setIsShowPrompt(false)
            },
          ],
          [
            "No",
            () => {
              setIsShowPrompt(false)
            },
          ],
        ],
      })
      setIsShowPrompt(true)
    },
    editSegText: () => {},
  }

  const closePanel = () => {
    setRightClickPanelData({ isShow: false })
    if (operatedData.type === "seg") {
      setOperatedData({ type: "none", content: "" })
    }
  }

  const choiceNodes = choices.map((choice) => {
    const [iconType, text, workType] = choice
    return (
      <div className={s.choice} key={workType} onClick={handlers[workType]}>
        <i className={iconClassNames[iconType] + " " + s.icon} />
        {text}
      </div>
    )
  })

  return (
    <>
      <div
        className={s.transparentScreen}
        onClick={closePanel}
        onContextMenu={closePanel}
      />
      <div className={s.panel} style={{ left: xY[0], top: xY[1] }}>
        {choiceNodes}
        {/* <div className={s.choice}> */}
        {/*  <i className={"fa-solid fa-copy " + s.icon} /> */}
        {/*  Copy text */}
        {/* </div> */}
        {/* <div className={s.choice}> */}
        {/*  <i className={"fa-solid fa-pen " + s.icon} /> */}
        {/*  Edit */}
        {/* </div> */}
        {/* <div className={s.choice}> */}
        {/*  <i className={"fa-solid fa-code " + s.icon} /> */}
        {/*  Inspect */}
        {/* </div> */}
      </div>
    </>
  )
}
export default RightClickPanel
