import s from "./FileSettingPanel.module.scss"
import React, { useContext, useEffect, useState } from "react"
import IntertextContext from "./IntertextContext"
import { getBasicStamp, getZonedStamp } from "../../../utils/timeStamp"
import "@fortawesome/fontawesome-free/js/brands"
import "@fortawesome/fontawesome-free/js/solid"
import "@fortawesome/fontawesome-free/js/fontawesome"
function FileSettingPanel(props) {
  const { onFinish, isInitialized, returnToEmpty, segs } = props
  const { operatedData, setOperatedData } = useContext(IntertextContext)
  const metaData = operatedData.content
  const { title, fileNameStyle, toBottom, saveLeaveStatus } = metaData

  const processedTitle = title.replace(/[\\/:*?"<>|]/g, "#")
  const previewedTitle =
    processedTitle.length > 40
      ? processedTitle.substring(0, 32) +
        "..." +
        processedTitle.substring(processedTitle.length - 8)
      : processedTitle

  // 不包含\/:*?"<>| -> #

  const onTitleInputChange = (newTitle) => {
    setOperatedData({
      type: "metaData",
      content: {
        ...metaData,
        title: newTitle,
      },
    })
  }
  const onToTopOrBottomToggleChange = () => {
    setOperatedData({
      type: "metaData",
      content: {
        ...metaData,
        toBottom: !toBottom,
      },
    })
  }

  const saveLeavePromptTexts = {
    default: "Save and leave",
    processing: "Processing...",
    confirmDownloaded: "Saved? Then tap again",
    okToLeave: "Leave now",
  }
  const changeSaveLeaveStatus = (newStatus) => {
    setOperatedData({
      type: "metaData",
      content: {
        ...metaData,
        saveLeaveStatus: newStatus,
      },
    })
  }

  const saveAndDownload = () => {
    if (isInitialized) {
      if (saveLeaveStatus === "default") {
        changeSaveLeaveStatus("processing")

        const now = new Date()
        const basicStamp = getBasicStamp(now)
        const intertextData = {
          meta: { ...metaData, timeStamp: now.getTime() },
          segs: segs.map((seg) => [seg[1], seg[2]]),
        }
        const intertextFile = new Blob([JSON.stringify(intertextData)], {
          type: "text/plain",
        })
        const url = URL.createObjectURL(intertextFile)
        const link = document.createElement("a")
        link.href = url
        link.download =
          basicStamp +
          " " +
          metaData.title.replace(/[\\/:*?"<>|]/g, "#") +
          ".json" // 就是文件名
        link.click()
        window.URL.revokeObjectURL(url)
        changeSaveLeaveStatus("confirmDownloaded")
      } else if (saveLeaveStatus === "confirmDownloaded") {
        changeSaveLeaveStatus("okToLeave")
      } else if (saveLeaveStatus === "okToLeave") {
        onFinish(false) // 这个就会clear operatedData了
        returnToEmpty()
      }
    }
  }

  const [now] = useState(new Date())
  const basicStamp = getBasicStamp(now)

  return (
    <>
      <div className={s.background} onClick={() => onFinish(false)} />
      <div className={s.panel}>
        <div className={s.titleSetting}>
          <div className={s.label}>Title:</div>
          <input
            type="text"
            value={title}
            onChange={(ev) => onTitleInputChange(ev.target.value)}
            className={s.titleInput}
            spellCheck={false}
          />
        </div>

        <div className={s.fileNameFormatSetting}>
          <div className={s.label}>File name format:</div>

          {/* <i className={"fa-solid fa-repeat" + " " + s.switchIcon} /> */}
        </div>
        <div className={s.fileNamePreview}>
          {basicStamp +
            (previewedTitle.length !== 0 ? " " + previewedTitle : "") +
            ".json"}
        </div>
        <div className={s.setToTopOrBottom}>
          <div className={s.label}>When opened, go to: </div>
          <div className={s.toggle} onClick={onToTopOrBottomToggleChange}>
            {toBottom ? "bottom" : "top"}
          </div>
        </div>
        <div className={s.buttons}>
          <div className={s.leftButtons}>
            {isInitialized && (
              <div
                className={
                  s.leave +
                  " " +
                  (saveLeaveStatus === "processing" ? s.processing : "")
                }
                onClick={saveAndDownload}
              >
                {saveLeavePromptTexts[saveLeaveStatus]}
              </div>
            )}
          </div>
          <div className={s.rightButtons}>
            <div className={s.ok} onClick={() => onFinish(true, metaData)}>
              {isInitialized ? "OK" : "Start"}
            </div>
            <div className={s.cancel} onClick={() => onFinish(false)}>
              Cancel
            </div>
          </div>
        </div>
      </div>
    </>
  )
}
export default FileSettingPanel
