import React, { useContext } from "react"
import s from "./Seg.module.scss"
import IntertextContext from "./IntertextContext"

function Seg(props) {
  const { segData, isFirstSeg } = props
  const typeNum = segData[1]
  const text = segData[2]

  const intertextContext = useContext(IntertextContext)
  const { operatedData, setOperatedData, setSegTouchPanelData } =
    intertextContext

  const classNames = [s.leftSeg, s.rightSeg, s.centerSeg]

  let isFocused = false
  if (operatedData.type === "seg" || operatedData.type === "segs") {
    isFocused = operatedData.content.includes(segData)
  }

  const onClick = (ev) => {
    ev.preventDefault()
    setOperatedData({
      type: "seg",
      content: [segData],
    })
    const choices = isFirstSeg
      ? ["edit", "insertAhead", "insertBehind", "delete"]
      : ["edit", "insertBehind", "delete"]
    setSegTouchPanelData({
      isShow: true,
      xY: [ev.clientX, ev.clientY],
      choices,
    })
  }

  return (
    <div
      className={classNames[typeNum] + " " + (isFocused ? s.focused : "")}
      onClick={onClick}
    >
      {text}
    </div>
  )
}
export default Seg
