import s from "./Wikipedia.module.scss"
import { useParams } from "react-router-dom"
import { useEffect, useState } from "react"

function Wikipedia() {
  useEffect(() => {
    document.title = "wikipedia tool"
  }, [])
  const { title } = useParams()
  const processedTitle = title.replaceAll("_", " ")
  const [html, setHtml] = useState(
    "<div>Fetching the content of <b>" + processedTitle + "</b></div>"
  )

  const url =
    "https://en.wikipedia.org/w/api.php?" +
    new URLSearchParams({
      origin: "*",
      action: "parse",
      page: title,
      format: "json",
    })
  fetch(url)
    .then((res) => res.json())
    .then((json) => {
      const originalHtml = json.parse.text["*"]
      const processedHtml = originalHtml
        .replaceAll("/wiki/", "/wikipedia/")
        .replaceAll(/"\/wikipedia\/File:(\S*)"/g, '"javascript:void(0)"')
      setHtml(processedHtml)
    })

  return (
    <>
      <div className={"wiki-content"}>
        <div className={"wiki-title"}>{processedTitle}</div>
        <div
          className={"wiki-main"}
          dangerouslySetInnerHTML={{ __html: html }}
        />
      </div>
    </>
  )
}
export default Wikipedia
