import s from "./SegTouchPanel.module.scss"
import React, { useContext } from "react"
import PromptContext from "../../../components/Prompt/mobile/PromptContext"
import IntertextContext from "./IntertextContext"

// (选中文本)复制，inspect，以及其他功能
// delete

function SegTouchPanel() {
  const intertextContext = useContext(IntertextContext)
  const promptContext = useContext(PromptContext)

  const {
    operatedData,
    setOperatedData,
    operations,
    segTouchPanelData,
    setSegTouchPanelData,
  } = intertextContext
  const { promptData, setIsShowPrompt, setPromptData } = promptContext

  const { isShow, xY, choices } = segTouchPanelData

  const choiceTypes = {
    edit: ["Edit", "editSegText", "fa-solid fa-pen"],
    insertAhead: ["Insert Ahead", "insertSegAhead", "fa-solid fa-turn-up"],
    insertBehind: ["Insert behind", "insertSegBehind", "fa-solid fa-turn-down"],
    delete: ["Delete", "deleteOneSeg", "fa-solid fa-xmark"],
  }
  const handlers = {
    editSegText: () => {
      setPromptData({
        title: "Edit",
        toggles: {
          current: ["Left", "Right", "Center"][operatedData.content[0][1]],
          all: ["Left", "Center", "Right"],
        },
        textareaText: operatedData.content[0][2],
        buttons: [
          [
            "OK",
            (promptData) => {
              if (promptData.textareaText.trim() !== "") {
                operations.editSeg(
                  promptData.textareaText.trim(),
                  { Left: 0, Right: 1, Center: 2 }[promptData.toggles.current]
                )
                setSegTouchPanelData({ isShow: false })
                setOperatedData({ type: "none" })
                setIsShowPrompt(false)
              }
            },
          ],
          [
            "Cancel",
            () => {
              setIsShowPrompt(false)
            },
          ],
        ],
      })
      setIsShowPrompt(true)
    },
    insert: (isBehind) => {
      setPromptData({
        title: isBehind ? "Insert behind" : "Insert ahead",
        toggles: {
          current: ["Left", "Right", "Center"][operatedData.content[0][1]],
          all: ["Left", "Center", "Right"],
        },
        textareaText: "",
        buttons: [
          [
            "OK",
            (promptData) => {
              if (promptData.textareaText.trim() !== "") {
                operations.insert(
                  promptData.textareaText.trim(),
                  { Left: 0, Right: 1, Center: 2 }[promptData.toggles.current],
                  isBehind
                )
                setSegTouchPanelData({ isShow: false })
                setOperatedData({ type: "none" })
                setIsShowPrompt(false)
              }
            },
          ],
          [
            "Cancel",
            () => {
              setIsShowPrompt(false)
            },
          ],
        ],
      })
      setIsShowPrompt(true)
    },
    insertSegAhead: () => handlers.insert(false),
    insertSegBehind: () => handlers.insert(true),
    deleteOneSeg: () => {
      setPromptData({
        title: "Delete this text?",
        content: operatedData.content[0][2],
        buttons: [
          [
            "Yep",
            () => {
              operations.deleteSeg()
              setSegTouchPanelData({ isShow: false })
              setOperatedData({ type: "none" })
              setIsShowPrompt(false)
            },
          ],
          [
            "No",
            () => {
              setIsShowPrompt(false)
            },
          ],
        ],
      })
      setIsShowPrompt(true)
    },
  }

  const closePanel = () => {
    setSegTouchPanelData({ isShow: false })
    if (operatedData.type === "seg") {
      setOperatedData({ type: "none", content: "" })
    }
  }

  const choiceNodes = choices.map((choice) => {
    const [text, handler, iconClassName] = choiceTypes[choice]
    return (
      <div className={s.choice} key={choice} onClick={handlers[handler]}>
        <i className={iconClassName + " " + s.icon} />
        {text}
      </div>
    )
  })

  const [x, y] = xY
  const style = {}
  if (x > window.innerWidth / 2) {
    style.right = window.innerWidth - x
  } else {
    style.left = x
  }
  if (y > window.innerHeight / 2) {
    style.bottom = window.innerHeight - y
  } else {
    style.top = y
  }

  return (
    <>
      <div
        className={s.transparentScreen}
        onClick={closePanel}
        onContextMenu={closePanel}
      />
      <div className={s.panel} style={style}>
        {choiceNodes}
        {/* <div className={s.choice}> */}
        {/*  <i className={"fa-solid fa-copy " + s.icon} /> */}
        {/*  Copy text */}
        {/* </div> */}
        {/* <div className={s.choice}> */}
        {/*  <i className={"fa-solid fa-pen " + s.icon} /> */}
        {/*  Edit */}
        {/* </div> */}
        {/* <div className={s.choice}> */}
        {/*  <i className={"fa-solid fa-code " + s.icon} /> */}
        {/*  Inspect */}
        {/* </div> */}
      </div>
    </>
  )
}
export default SegTouchPanel
