import s from "./Test.module.scss"
import { useCallback, useEffect, useMemo, useRef, useState } from "react"
import TestContext from "./TestContext"

import { createEditor, Editor, Transforms, Text } from "slate"
import { Slate, Editable, withReact } from "slate-react"
import { withHistory } from "slate-history" // 这个需要yarn！

// 不要搞多了 简洁简单为主
// 着重：加粗+颜色亮一点#eeeeee b  某种彩色比如金色gold e  颜色#666666 融入的  代码文本（行内和跨行，相关语言）c  ol一行行的o
// 正常字号0  大字号1  大大字号2  居中m  链接a  引用
// 定位 id="xxx" 然后#xxx会定位到这里 这个可以让用户自己设置？不 就是大字号可以定位(右键生成定位链接？)
// 文本替换快捷键：比如我可以自己设定dotless i和j的替换

function Test(props) {
  useEffect(() => {
    document.title = "test"
  }, [])

  const [text, setText] = useState("")

  // Slate

  // const [editor] = useState(() => withReact(createEditor()))
  const editor = useMemo(() => withHistory(withReact(createEditor())), []) // 加了withHistory就可以ctrl+z了
  const [value, setValue] = useState([
    // 这里的value就是基础数据了
    {
      type: "paragraph",
      children: [{ text: "really?" }],
    },
  ])
  const CustomEditor = {
    isBold() {
      const [match] = Editor.nodes(editor, {
        match: (n) => n.bold === true,
        universal: true,
      })
      return match
    },
    toggleBold: (ev) => {
      ev.preventDefault()
      const isActive = CustomEditor.isBold()
      Transforms.setNodes(
        editor,
        { bold: isActive ? null : true },
        { match: (n) => Text.isText(n), split: true }
      )
    },
  }

  const Leaf = (props) => {
    return (
      <span
        {...props.attributes}
        style={{ fontWeight: props.leaf.bold ? "bold" : "normal" }}
      >
        {props.children}
      </span>
    )
  }
  const renderLeaf = useCallback((props) => {
    return <Leaf {...props} />
  }, [])

  return (
    <>
      <Slate
        editor={editor}
        value={value}
        onChange={(value) => setValue(value)}
      >
        <Editable
        // spellCheck={false}
        // renderLeaf={renderLeaf}
        // onKeyDown={(ev) => {
        //   if (ev.ctrlKey && ev.key === "b") {
        //     console.log("here")
        //     CustomEditor.toggleBold(ev)
        //   }
        // }}
        />
      </Slate>
      <div className={s.line} />
      <span className={s.outer}>
        <span className={s.inner}>test</span>
      </span>

      <div onMouseDown={CustomEditor.toggleBold}>BOLD</div>
      {/* 还没解决一点击就失去了这边的焦点的问题 */}
      {/* 噢 通过onMouseDown加上preventDefault来解决失去焦点的问题！！！ */}

      <div className={s.line} />
      <div className={s.textareaFrame}>
        <div
          className={
            s.what + " " + (text[text.length - 1] === "\n" ? s.extraLine : "")
          }
        >
          {text}
        </div>
        <textarea
          className={s.textarea}
          value={text}
          spellCheck={false}
          onChange={(ev) => setText(ev.target.value)}
        />
      </div>
      <div>test</div>
      <div>test</div>
    </>
  )
}
export default Test
