import { useEffect, useState } from "react"
import s from "./SculptStart.module.scss"
import Sculpt from "./Sculpt"

function SculptStart() {
  useEffect(() => {
    document.title = "Sculpt - oustr.github.io"
  }, [])
  const [isInitialized, setIsInitialized] = useState(false)
  const [isSettingDatabase, setIsSettingDatabase] = useState(false)
  const [isUsingDatabase, setIsUsingDatabase] = useState(false)
  const [isHttps, setIsHttps] = useState(false)
  const [urlContent, setURLContent] = useState("")
  const [url, setURL] = useState("")

  const updateURLContent = (text) => {
    text = text.replace(/[^a-zA-Z0-9:/\-_]/g, "")
    setURLContent(text)
  }

  const yes = () => {
    setIsSettingDatabase(true)
  }
  const no = () => {
    setURL("")
    setIsInitialized(true)
  }
  const ok = () => {
    const url =
      (isHttps ? "https" : "http") +
      "://localhost" +
      urlContent.replace(/[^a-zA-Z0-9:/\-_]/g, "")
    setURL(url)
    setIsInitialized(true)
    setIsSettingDatabase(false)
  }
  const cancel = () => {
    setIsSettingDatabase(false)
  }

  const showIAmTired = () => {
    const text =
      "This feature is usable, but I haven't written the documentation yet because I am too tired. " +
      "If you are interested, you can ask me and I will write the documentation."
    window.alert(text)
  }

  return (
    <>
      {isInitialized ? (
        <Sculpt databaseUrl={url} setIsInitialized={setIsInitialized} />
      ) : (
        <>
          <div className={s.frame}>
            <div className={s.title}>SCULPT</div>
            <div className={s.panel}>
              <div className={s.left}>
                {isSettingDatabase ? (
                  <>
                    <div className={s.url}>
                      <span
                        className={s.protocol}
                        onClick={() => setIsHttps(!isHttps)}
                      >
                        {isHttps ? "https" : "http"}
                      </span>
                      <span>://localhost</span>
                      <textarea
                        value={urlContent}
                        onChange={(ev) => updateURLContent(ev.target.value)}
                        className={s.textarea}
                        spellCheck={false}
                        autoFocus
                      />
                    </div>
                  </>
                ) : (
                  <div className={s.prompt}>
                    Use a local{" "}
                    <a
                      href={"javascript:void(0)"}
                      className={s.link}
                      onClick={showIAmTired}
                    >
                      database for Sculpt?
                    </a>{" "}
                    (If you have)
                  </div>
                )}
              </div>

              <div className={s.buttons}>
                {isSettingDatabase ? (
                  <>
                    <div className={s.ok} onClick={ok}>
                      OK
                    </div>
                    <div className={s.cancel} onClick={cancel}>
                      Cancel
                    </div>
                  </>
                ) : (
                  <>
                    <div className={s.yes} onClick={yes}>
                      Yes
                    </div>
                    <div className={s.no} onClick={no}>
                      No
                    </div>
                  </>
                )}
              </div>
            </div>
          </div>
        </>
      )}
    </>
  )
}
export default SculptStart
