import s from "./Intertext.module.scss"
import React, { useContext, useEffect, useRef, useState } from "react"
import "@fortawesome/fontawesome-free/js/brands"
import "@fortawesome/fontawesome-free/js/solid"
import "@fortawesome/fontawesome-free/js/fontawesome"
import Prompt from "../../../components/Prompt/mobile/Prompt"
import PromptContext from "../../../components/Prompt/mobile/PromptContext"
import SegTouchPanel from "./SegTouchPanel"
import Seg from "./Seg"
import IntertextContext from "./IntertextContext"
import { getBasicStamp } from "../../../utils/timeStamp"
import FileSettingPanel from "./FileSettingPanel"

function Intertext() {
  useEffect(() => {
    document.title = "Intertext - oustr.github.io"
  }, [])

  import("./Intertext.scss")
  // How to use | demo
  // 原则：没有那么多精力，那就做最最关键的功能

  // 应该要有返回oustr.github.io首页的方法

  // Focus功能 点击背景板或者其他的才消失 作用就是好看 标记

  // before you write, make sure...保证可以下载而不会跳转页面!!! embedded

  const [segs, setSegs] = useState([])
  const [metaData, setMetaData] = useState({
    title: "",
    timeStamp: 0,
    fileNameStyle: "YYYYMMDD-HHMMSS",
    toBottom: true,
  })
  const [isInitialized, setIsInitialized] = useState(false)

  const [layoutType, setLayoutType] = useState("normal")

  // import和new
  // Are you new here? See the [How to use]可按 demo.
  // 到时候点击文件名可以import和new
  // text
  // meta数据要兼容以前老版本？先不管
  // meta数据还要有当前的文档版本 1.0
  // meta里面要有：打开时到最底部/顶部
  // meta数据：title, timeStamp (纪元秒), fileNameStyle, toBottom
  // metaData读取的时候 如果没有就""/bottom
  // 做个兼容吧 如果只是[..]这种的，那就系统帮你设置metaData

  // 有几条记录后的自动下载功能

  // Save and leave
  // Downloaded? Leave

  const [operatedData, setOperatedData] = useState({
    type: "none", // seg, metaData ... segs, selectedText
    content: "", // seg或者segs这里直接应该是[[segKey, "0", "内容"],[...]]这种, selectedText就是"..."
  })

  const operations = {
    editSeg: (newText, sideNum) => {
      const newSegs = [
        ...segs.map((seg) => {
          if (operatedData.content.includes(seg)) {
            seg[1] = sideNum
            seg[2] = newText.trim()
          }
          return seg
        }),
      ]
      setSegs(newSegs)
    },
    insert: (newText, sideNum, isBehind) => {
      const newSeg = [Date.now(), sideNum, newText.trim()]
      const newSegs = []
      if (isBehind) {
        segs.map((seg) => {
          newSegs.push(seg)
          if (operatedData.content.includes(seg)) {
            newSegs.push(newSeg)
          }
        })
        setSegs(newSegs)
      } else {
        setSegs([newSeg, ...segs])
      }
    },
    deleteSeg: () => {
      const newSegs = [
        ...segs.filter((seg) => !operatedData.content.includes(seg)),
      ]
      setSegs(newSegs)
    },
  }
  const [segTouchPanelData, setSegTouchPanelData] = useState({
    isShow: false,
    xY: [0, 0],
    choices: [], // [["delete", "Delete", "deleteOneSeg"]] 第一个是图标 第二个是text 第三个是提醒做什么的
  })

  const segsContentRef = useRef()
  const segTextInputRef = useRef()

  // 强制回收站 你删除的东西都会记录起来保存在localstorage里

  // 用index和时间戳标记key
  // ctrl+z 还早呢
  // 格式化复制 左:xxx [2023/3/6] 右:xxx
  // 导出图片 / 便于pdf打印

  // invent sth to take notes of coding (main text / comments in two different colors)

  const [whichSegToSend, setWhichSegToSend] = useState("R")
  const [textareaText, setTextareaText] = useState("")
  const fileInputRef = useRef()

  const [isShowTopShade, setIsShowTopShade] = useState(false)
  const [isShowBottomShade, setIsShowBottomShade] = useState(false)

  const [isShowPrompt, setIsShowPrompt] = useState(false)

  const [promptData, setPromptData] = useState({})

  const [toTopOrBottom, setToTopOrBottom] = useState("")
  const [prevSegsContentScrollTop, setPrevSegsContentScrollTop] = useState(0)

  useEffect(() => {
    window.intertext = {
      setSegTouchPanelData,
      setOperatedData,
    }
    // 不行，只是传了数值
  }, [])
  useEffect(() => {
    window.intertext.isShowPrompt = isShowPrompt
  }, [isShowPrompt])
  useEffect(() => {
    window.intertext.isShowSegTouchPanel = segTouchPanelData.isShow
  }, [segTouchPanelData])
  useEffect(() => {
    window.intertext.operatedDataType = operatedData.type
  }, [operatedData])

  const [enterToWhat, setEnterToWhat] = useState("Enter to send")
  const toggleEnterToWhat = () => {
    segTextInputRef.current.focus()
    if (enterToWhat === "Enter to send") {
      setEnterToWhat("Enter to nextline")
    } else {
      setEnterToWhat("Enter to send")
    }
  }

  useEffect(() => {
    if (!segsContentRef.current) {
      setToTopOrBottom("")
    } else {
      if (
        segsContentRef.current.scrollHeight <=
        segsContentRef.current.clientHeight
      ) {
        setToTopOrBottom("")
      }
    }
  }, [segs])

  const onSegsContentScroll = () => {
    const { scrollTop, scrollHeight, clientHeight } = segsContentRef.current
    setPrevSegsContentScrollTop(scrollTop)
    // isWithin就是是不是在 100-中间-100 而且中间至少400长
    const isWithin =
      scrollTop > 100 &&
      scrollHeight - (scrollTop + clientHeight) > 100 &&
      scrollHeight - clientHeight > 400
    if (isWithin) {
      if (segsContentRef.current.scrollTop - prevSegsContentScrollTop >= 0) {
        setToTopOrBottom("toBottom")
      } else {
        setToTopOrBottom("toTop")
      }
    } else {
      setToTopOrBottom("")
    }
  }

  // 文档的// import是在这里
  const clickFileInput = () => {
    fileInputRef.current.click() // 虽然没有提示但是是可以用的
  }
  const importFile = async (hasJsonData, jsonData) => {
    let json = {}
    if (hasJsonData) {
      json = jsonData
    } else {
      if (fileInputRef) {
        json = await fetch(
          URL.createObjectURL(fileInputRef.current.files[0])
        ).then((res) => res.json())
      }
    }
    if (json instanceof Array) {
      json = {
        meta: {
          title: "NO TITLE",
          timeStamp: Date.now(),
          fileNameStyle: "YYYYMMDD-HHMMSS-TZ", // 20230312-170622-E11 Title.int 点击来切换 // 保存在cookie里？
          toBottom: true,
        },
        segs: [...json],
      }
    }
    setMetaData(json.meta)
    const tempSegs = []
    json.segs.map((seg, index) => {
      tempSegs.push([index, ...seg])
    })
    setSegs(tempSegs)

    setTimeout(() => {
      if (json.meta.toBottom) {
        goToBottom(true)
      } else {
        goToTop(true)
      }
      setIsInitialized(true)
    }, 100)

    // if (fileInputUrl !== "") {
    //   // const filePath = fileInputRef.current.value
    //   // const fileName = filePath.split(/[/\\]/).pop()
    //   fetch(URL.createObjectURL(fileInputUrl))
    //     .then((res) => res.json())
    //     .then((json) => {
    //
    //     })
    // }
  }
  const editMetaData = () => {
    setOperatedData({
      type: "metaData",
      content: { ...metaData, saveLeaveStatus: "default" },
      // saveLeaveStatus: default -> processing -> confirmDownloaded -> okToLeave 用于FileSettingPanel
    })
  }
  const finishEditMetaData = (isOK, metaData) => {
    if (isOK) {
      setMetaData(metaData)
      setIsInitialized(true)
    }
    clearOperatedData()
  }
  const onClickSend = () => {
    segTextInputRef.current.focus()
    if (textareaText.trim()) {
      const newSegs = [...segs]
      const sideNum = {
        L: 0,
        R: 1,
        C: 2,
      }
      newSegs.push([Date.now(), sideNum[whichSegToSend], textareaText.trim()])
      setTextareaText("")
      setSegs(newSegs)
      goToBottom(false)
    }
  }

  const onKeyDown = (ev) => {
    if (!segTouchPanelData.isShow && !isShowPrompt) {
      if (ev.shiftKey && ev.key === "ArrowLeft") {
        setWhichSegToSend("L")
        ev.preventDefault()
      }
      if (ev.shiftKey && ev.key === "ArrowRight") {
        setWhichSegToSend("R")
        ev.preventDefault()
      }
      if (ev.shiftKey && ev.key === "ArrowUp") {
        setWhichSegToSend("C")
        ev.preventDefault()
      }
      if (ev.key === "Enter" && enterToWhat === "Enter to send") {
        onClickSend()
        ev.preventDefault()
      }
    }
  }
  useEffect(() => {
    document.addEventListener("keydown", onKeyDown, false)
    return () => {
      document.removeEventListener("keydown", onKeyDown, false)
    }
  })

  const clearOperatedData = () => {
    if (operatedData.type !== "none") {
      setOperatedData({ type: "none", content: "" })
    }
  }

  // 这里面检测isShowPrompt的时候会薛定谔的bool？？？
  // 可能是直接加在window上面的事件的问题
  // 我的天啊！！！！怎么能直接把addEventListener写在这里！！每次刷新都要加事件！！！
  // 这里面的数据都不会动 不能直接读取外面的东西
  useEffect(() => {
    window.addEventListener("resize", () => {
      const {
        isShowPrompt,
        isShowSegTouchPanel,
        setSegTouchPanelData,
        operatedDataType,
        setOperatedData,
      } = window.intertext
      if (isShowSegTouchPanel) {
        setSegTouchPanelData({ isShow: false, xY: [], choices: [] })
      }
      if (operatedDataType === "seg" && !isShowPrompt) {
        setOperatedData({ type: "none", content: "" })
      }
    })
  }, [])

  const segNodes = segs.map((seg) => {
    return <Seg key={seg[0]} segData={seg} isFirstSeg={segs[0] === seg} />
  })

  // 随时保存segs内容到localStorage
  // 可能要提醒一下目前localStorage这个存了多大了 1MB=1048576字节
  // useEffect(() => {
  //   if (segs.length !== 0) {
  //     localStorage.setItem("intertext-unsaved-text", JSON.stringify(segs))
  //   }
  //   return () => {
  //     console.log(localStorage.getItem("intertext-unsaved-text"))
  //   }
  // }, [segs])

  // Powered by React
  // How to use?

  const saveAndDownload = () => {
    if (isInitialized) {
      const now = new Date()
      const basicStamp = getBasicStamp(now)
      const intertextData = {
        meta: { ...metaData, timeStamp: now.getTime() },
        segs: segs.map((seg) => [seg[1], seg[2]]),
      }
      const intertextFile = new Blob([JSON.stringify(intertextData)], {
        type: "text/plain",
      })
      const url = URL.createObjectURL(intertextFile)
      const link = document.createElement("a")
      link.href = url
      if (metaData.title.length !== 0) {
        link.download =
          basicStamp +
          " " +
          metaData.title.replace(/[\\/:*?"<>|]/g, "#") +
          ".json" // 就是文件名
      } else {
        link.download = basicStamp + ".json"
      }

      link.click()
      window.URL.revokeObjectURL(url)
    }
  }

  const changeWhichSegToSend = (which) => {
    segTextInputRef.current.focus()
    if (which !== whichSegToSend) {
      setWhichSegToSend(which)
    }
  }

  const whichSegToSendButtons = ["L", "C", "R"].map((which) => {
    const className =
      s.whichSegToSendButton +
      " " +
      (which === whichSegToSend ? s.selected : "")
    return (
      <div
        className={className}
        key={which}
        onClick={() => changeWhichSegToSend(which)}
      >
        {which}
      </div>
    )
  })

  const goToTop = (hasEffect) => {
    if (segsContentRef.current) {
      setIsShowTopShade(false)
      setTimeout(() => {
        segsContentRef.current.scrollTop = 0
        if (hasEffect) {
          setIsShowTopShade(true)
        }
      }, 10)
    }
  }
  const goToBottom = (hasEffect) => {
    if (segsContentRef.current) {
      setIsShowBottomShade(false)
      setTimeout(() => {
        segsContentRef.current.scrollTop = 1000000
        if (hasEffect) {
          setIsShowBottomShade(true)
        }
      }, 10)
    }
  }

  const [touchTimeout, setTouchTimeout] = useState(null)
  const onTouchStart = () => {
    const timeout = setTimeout(() => {}, 1000)
    setTouchTimeout(timeout)
  }
  const onTouchFinish = () => {
    if (touchTimeout !== null) {
      clearTimeout(touchTimeout)
    }
    setTouchTimeout(null)
  }

  const returnToEmpty = () => {
    setTextareaText("")
    setWhichSegToSend("R")
    setEnterToWhat("Enter to send")
    setSegs([])
    setMetaData({
      title: "",
      timeStamp: 0,
      fileNameStyle: "YYYYMMDD-HHMMSS",
      toBottom: true,
    })
    setIsInitialized(false)
  }

  const openHowToUse = async () => {
    const { meta, segs } = await import(
      "../../../asset/how-to-use-intertext.json"
    )
    importFile(true, { meta, segs })
  }
  // context记笔记！

  const [viewHeight, setViewHeight] = useState(visualViewport.height)
  useEffect(() => {
    window.addEventListener("resize", () => {
      setViewHeight(visualViewport.height)
    })
  }, [])

  return (
    <IntertextContext.Provider
      value={{
        operatedData,
        setOperatedData,
        operations,
        segTouchPanelData,
        setSegTouchPanelData,
      }}
    >
      <div className={s.root}>
        <PromptContext.Provider
          value={{ isShowPrompt, setIsShowPrompt, promptData, setPromptData }}
        >
          <Prompt>
            <div className={s.mobileLayout} style={{ height: viewHeight }}>
              <div className={s.fileBar}>
                {!isInitialized && (
                  <div className={s.leftButtons}>
                    <div className={s.importButton} onClick={clickFileInput}>
                      Import
                    </div>
                    <div className={s.newButton} onClick={editMetaData}>
                      New
                    </div>
                  </div>
                )}

                {isInitialized && (
                  <div className={s.textTitle} onClick={editMetaData}>
                    {metaData.title === "" ? "[NO TITLE]" : metaData.title}
                  </div>
                )}
                <input
                  type="file"
                  className={s.fileInput}
                  ref={fileInputRef}
                  onChange={() => importFile(false)}
                />
                {isInitialized && (
                  <div
                    className={
                      s.saveButton + " " + (!isInitialized && s.disabled)
                    }
                    onClick={saveAndDownload}
                  >
                    Save
                  </div>
                )}
              </div>

              <div className={s.segsFrame}>
                {segs.length !== 0 ? (
                  <div
                    className={s.segsContent}
                    ref={segsContentRef}
                    onScroll={onSegsContentScroll}
                    onClickCapture={clearOperatedData}
                    onTouchStartCapture={onTouchStart}
                    onTouchEndCapture={onTouchFinish}
                    onTouchCancelCapture={onTouchFinish}
                  >
                    {isShowTopShade && <div className={s.topShade} />}
                    {segNodes}
                    {isShowBottomShade && <div className={s.bottomShade} />}
                  </div>
                ) : (
                  <div
                    className={s.noTextBackground}
                    onTouchStartCapture={onTouchStart}
                    onTouchEndCapture={onTouchFinish}
                    onTouchCancelCapture={onTouchFinish}
                  >
                    <div className={s.noText}>
                      NO TEXT
                      {!isInitialized && (
                        <div className={s.howToUse} onClick={openHowToUse}>
                          How to use
                        </div>
                      )}
                    </div>
                  </div>
                )}
              </div>

              {isInitialized && (
                <div className={s.textPanel}>
                  {toTopOrBottom === "toTop" && (
                    <div
                      className={s.toTopOrBottomButton}
                      onClick={() => goToTop(true)}
                    >
                      <i className="fa-solid fa-angle-up" />
                    </div>
                  )}
                  {toTopOrBottom === "toBottom" && (
                    <div
                      className={s.toTopOrBottomButton}
                      onClick={() => goToBottom(true)}
                    >
                      <i className="fa-solid fa-angle-down" />
                    </div>
                  )}
                  <div className={s.segTextInputPanel}>
                    <textarea
                      ref={segTextInputRef}
                      className={s.segTextInput}
                      spellCheck={false}
                      value={textareaText}
                      onChange={(ev) => setTextareaText(ev.target.value)}
                    />
                    <div className={s.buttons}>
                      <div className={s.leftButtons}>
                        {whichSegToSendButtons}
                        <div
                          className={s.nextLineToggleButton}
                          onClick={toggleEnterToWhat}
                        >
                          {enterToWhat}
                        </div>
                      </div>
                      <div
                        className={
                          s.sendButton + " " + (!isInitialized && s.disabled)
                        }
                        onClick={onClickSend}
                      >
                        <i className={"fa-solid fa-location-arrow"} />
                      </div>
                    </div>
                  </div>
                </div>
              )}
            </div>

            {segTouchPanelData.isShow && <SegTouchPanel />}
            {operatedData.type === "metaData" && (
              <FileSettingPanel
                onFinish={finishEditMetaData}
                isInitialized={isInitialized}
                returnToEmpty={returnToEmpty}
                segs={segs}
              />
            )}
          </Prompt>
        </PromptContext.Provider>
      </div>
    </IntertextContext.Provider>
  )
}
export default Intertext
