// 做一个包着的，只要包着就能底层模糊＋弹窗
// 包括alert 就是那种只闪出一下然后逐渐消失的

import s from "./Prompt.module.scss"
import { useContext, useEffect, useRef, useState } from "react"
import PromptContext from "./PromptContext"

function Prompt(props) {
  const { isShowPrompt, setIsShowPrompt, promptData, setPromptData } =
    useContext(PromptContext)

  if (!isShowPrompt) {
    return (
      <>
        <div className={""}>{props.children}</div>
      </>
    )
  } else {
    const { title, content, toggles, textareaText, buttons, buttonsOffset } =
      promptData

    // toggles: {current: "a", all: ["a","b"]}
    let toggleNodes
    if (toggles !== undefined) {
      toggleNodes = toggles.all.map((toggle) => {
        if (toggle === toggles.current) {
          return (
            <div className={s.toggle + " " + s.current} key={toggle}>
              {toggle}
            </div>
          )
        } else {
          const onClick = () => {
            setPromptData({
              ...promptData,
              toggles: { current: toggle, all: toggles.all },
            })
          }
          return (
            <div className={s.toggle} onClick={onClick} key={toggle}>
              {toggle}
            </div>
          )
        }
      })
    }

    const buttonNodes = buttons.map((button) => {
      const [text, handler] = button
      return (
        <div
          className={s.button}
          key={text}
          onClick={() => handler(promptData)}
        >
          {text}
        </div>
      )
    })

    const buttonsOffsetStyle = {}
    if (buttonsOffset) {
      buttonsOffsetStyle.transform = "translateX(" + buttonsOffset + ")"
    }

    const [viewHeight, setViewHeight] = useState(visualViewport.height)
    useEffect(() => {
      window.addEventListener("resize", () => {
        setViewHeight(visualViewport.height)
      })
    }, [])

    return (
      <>
        <div className={s.blur}>{props.children}</div>
        <div className={s.shadow} style={{ height: viewHeight }} />
        <div className={s.promptFrame} style={{ height: viewHeight * 0.88 }}>
          <div className={s.prompt}>
            <div className={s.title}>{title}</div>

            {<div className={s.toggles}>{toggleNodes}</div>}

            {textareaText !== undefined && (
              <div className={s.textareaFrame}>
                <div className={s.textareaHidden}>
                  {textareaText === "" ||
                  textareaText[textareaText.length - 1] === "\n"
                    ? textareaText + "..."
                    : textareaText}
                </div>
                <textarea
                  className={s.textarea}
                  spellCheck={false}
                  value={textareaText}
                  onChange={(ev) =>
                    setPromptData({
                      ...promptData,
                      textareaText: ev.target.value,
                    })
                  }
                />
              </div>
            )}

            {content !== undefined && (
              <div className={s.content}>{content}</div>
            )}
            <div className={s.buttons} style={buttonsOffsetStyle}>
              {buttonNodes}
            </div>
          </div>
        </div>
      </>
    )
  }

  // promptData {buttons: [["Yes", 1], ["No", 0]], title: "...", content: ...}

  // 弹出来的时候禁止上下滚动！！！

  // const data = {
  //   title: "Delete this text?",
  //   content: "I'm just interested in this.",
  //   buttons: [["Yep"], ["No"]],
  // }
}
export default Prompt
